:root{
  --line-border-fill: #F8A88B;
  --line-border-empty: #bdbdbd;
  --component-width: 80vw;
}

.cont{
  -webkit-flex-direction: column;
          flex-direction: column;
  display: -webkit-flex;
  display: flex;
  margin: 0;
}

.cont h1{
  color: black;
  margin: 50px 0 50px;
  text-align: center;
}

.container{
  text-align: center;
  width: 80vw;
  max-width: 1000px;
  height: 700px;
}

.progress-container{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  max-width: 100%;
  position: relative;
}

.progress-container:before{
  content: "";
  background-color: #bdbdbd;
  background-color: var(--line-border-empty);
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  height: 4px;
  width: 100%;
  z-index: -1;
  -webkit-transform: 0.4s ease;
          transform: 0.4s ease;
}

.progress{
  background-color: #F8A88B;
  background-color: var(--line-border-fill);
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  height: 4px;
  width: var(--pro);
  z-index: -1;
  -webkit-transform: 0.4s ease;
          transform: 0.4s ease;
}

.circle{
  border-radius: 50px;
  height: 70px;
  width: 70px;
  border: 3px solid #b2bec3;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  overflow: hidden;
  transition: 0.3s ease;
  cursor: pointer;
  background-color: white;
}

.circle.active{
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: #F8A88B;
  width: 220px;
  border: none;
}


.icon{
  height: 70px;
  width: 70px;
  border-radius: 50px;
}

.icon-text{
  padding-right: 15px;
  font-size: 20px;
  padding-top: 23px;
  color: #fff;
}

.circle.active .icon{
  border: 3px solid #F8A88B;
}


.description-container{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 80vw;
  width: var(--component-width);
  margin-top: 50px;
  max-width: 1000px;
}

.description{
  opacity: 0;
  font-size: 20px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
  width: 600px;
  transition: opacity 1s;
  text-align: justify;
}

.description.active{
  opacity: 1;
  transition: opacity 1s;
  text-align: justify;
}

.description .details{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.description .details .title{
  font-weight: bold;
  color: #F8A88B;
}

